// IE  polyfills
import 'core-js/stable'
import 'regenerator-runtime/runtime'

// 
declare global {
  interface Window {
    console: Console
  }
}

//  console IE8 
if (typeof window !== 'undefined' && !window.console) {
  window.console = {
    log: () => {},
    error: () => {},
    warn: () => {},
    info: () => {},
    debug: () => {},
  } as Console
}
