/**
 * storage 
 */
/**
 * storage, localStorage
 * @param {*} key  key
 * @param {*} type session/local
 * @returns
 */
export const getStorage = (key: string, type = 'local') => {
  let result
  if (typeof window !== 'undefined') {
    if (type === 'session') {
      result = sessionStorage.getItem(key) || ''
    } else {
      result = localStorage.getItem(key) || ''
    }
    try {
      result = JSON.parse(result)
    } catch (error) {
      result = ''
    }
    return result
  }
}
/**
 *  storage , localStorage
 *
 * @param {*} key key
 * @param {*} param key
 * @param {*} type  session/local
 */
export const setStorage = (key: string, param: any, type = 'local') => {
  if (typeof window !== 'undefined') {
    if (type === 'session') {
      sessionStorage.setItem(key, JSON.stringify(param))
    } else {
      localStorage.setItem(key, JSON.stringify(param))
    }
  }
}
/**
 * storage , localStorage
 * @param {*} key  key
 * @param {*} type  session/local
 */
export const deleteStorage = (key: string, type = 'local') => {
  if (typeof window !== 'undefined') {
    if (type === 'session') {
      sessionStorage.removeItem(key)
    } else {
      localStorage.removeItem(key)
    }
  }
}
