import { MockMethod } from 'vite-plugin-mock'

// 
export enum SurgeryType {
  ELECTIVE = 'elective', // 
  EMERGENCY = 'emergency', // 
  DAY = 'day', // 
}

// 
const generatePatients = (count: number) => {
  const patients: any[] = []
  for (let i = 0; i < count; i++) {
    patients.push({
      id: `ZY${String(Math.floor(Math.random() * 100000)).padStart(6, '0')}`, // 
      name: ['', '', '', '', ''][Math.floor(Math.random() * 5)],
      gender: Math.random() > 0.5 ? '' : '',
      age: Math.floor(Math.random() * 50) + 20,
      bloodType: ['A', 'B', 'O', 'AB'][Math.floor(Math.random() * 4)],
      ward: ['', '', '', ''][Math.floor(Math.random() * 4)], // 
      bedNumber: `${Math.floor(Math.random() * 20) + 1}`,
      medicalRecordNumber: `BA${String(Math.floor(Math.random() * 100000)).padStart(6, '0')}`, // 
      operatingRoom: ['04', '13', '14', '15'][Math.floor(Math.random() * 4)] + '',
      sequence: Math.floor(Math.random() * 10) + 1, // 
      surgeryTime: new Date(Date.now() + Math.random() * 7 * 24 * 60 * 60 * 1000).toISOString(),
      surgeryName: ['', '', '', ''][
        Math.floor(Math.random() * 4)
      ],
      nursingLevel: ['', '', ''][Math.floor(Math.random() * 3)],
      allergies: Math.random() > 0.7 ? ['', '', ''][Math.floor(Math.random() * 3)] : '', // 
      insuranceType: ['', '', '', ''][Math.floor(Math.random() * 4)], // 
      surgeryType: Object.values(SurgeryType)[Math.floor(Math.random() * 3)], // 
      surgeryStatus: ['pending', 'in_progress', 'completed'][Math.floor(Math.random() * 3)],
      surgeonName: ['', '', ''][Math.floor(Math.random() * 3)], // 
      anesthesiologistName: ['', ''][Math.floor(Math.random() * 2)], // 
      circuitNurse: ['', '', ''][Math.floor(Math.random() * 3)], // 
      anesthesiaNumber: `MA${String(Math.floor(Math.random() * 10000)).padStart(4, '0')}`, // 
      anesthesiaMethod: ['', '', ''][Math.floor(Math.random() * 3)], // 
      // 
      flags: {
        isAllergic: Math.random() > 0.7, // 
        hasVTE: Math.random() > 0.8, // VTE
        hivStatus: Math.random() > 0.9 ? 'positive' : 'negative', // HIV
        hasArrearage: Math.random() > 0.9, // 
        syphilisStatus: Math.random() > 0.9 ? 'positive' : 'negative', // 
      },
    })
  }
  return patients
}

export default [
  {
    url: '/api/patient/list',
    method: 'post',
    response: () => {
      return {
        code: 200,
        success: true,
        data: {
          total: 20,
          list: generatePatients(20),
        },
      }
    },
  },
] as MockMethod[]
