<script setup lang="ts">
import { ref, computed, watch } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useTabsStore } from '@/stores/tabsStore'
import { useUserStore } from '@/stores/userStore'
import {
  Refresh,
  User,
  Document,
  TrendCharts,
  Setting,
  House,
  DataAnalysis,
  FullScreen,
  Aim,
} from '@element-plus/icons-vue'
import { MENU_ITEMS, ROUTER_PATH } from '@/constants/menu'
import avatarImg from '@/assets/avatar-small.png'
import logo from '@/assets/logo.jpg'
import { storeToRefs } from 'pinia'
import { onMounted } from 'vue'
import { ElMessage, ElTooltip } from 'element-plus'
import { usePatientInfoStore } from '@/stores/patientInfoStore'
import { SYSTEM_CONFIG, SURGERY_ROOMS } from '@/constants/global'

const router = useRouter()
const route = useRoute()
const tabsStore = useTabsStore()
const userStore = useUserStore()
const { userInfo } = storeToRefs(userStore)
const patientInfoStore = usePatientInfoStore()

const isFullscreen = ref(false)

// 
const toggleFullScreen = () => {
  if (!document.fullscreenElement) {
    document.documentElement.requestFullscreen()
    isFullscreen.value = true
  } else {
    document.exitFullscreen()
    isFullscreen.value = false
  }
}

// 
onMounted(() => {
  document.addEventListener('fullscreenchange', () => {
    isFullscreen.value = !!document.fullscreenElement
  })
})

onUnmounted(() => {
  document.removeEventListener('fullscreenchange', () => {
    isFullscreen.value = !!document.fullscreenElement
  })
})

// 
onMounted(() => {
  console.log('Component mounted, initializing user info')
  userStore.initUserInfo()
})

// 
const userName = computed(() => {
  console.log('Computing userName, userInfo:', userInfo.value)
  return userInfo.value?.username || ''
})

// 
const menuItems = ref<Common_API.MenuItem[]>(MENU_ITEMS)
// {id-}
const menuIconIdMap: Record<string, Component> = {
  '0': House, // 
  '1': User, // 
  '2': Document, // 
  '3': TrendCharts, // 
  '4': DataAnalysis, // 
  '5': Setting, // 
}

// 
const activeTopMenu = ref(menuItems.value[0].id)

// 
const handleTopMenuSelect = (index: string) => {
  const selectedMenu = menuItems.value.find((item) => item.id === index)
  if (!selectedMenu) return

  activeTopMenu.value = index

  // 
  if (selectedMenu.path) {
    handleMenuClick(selectedMenu)
    return
  }

  // 
  const firstNavigableItem = findFirstNavigableMenuItem(selectedMenu)
  if (firstNavigableItem) {
    handleMenuClick(firstNavigableItem)
  }
}

// 
const findFirstNavigableMenuItem = (menuItem: Common_API.MenuItem): Common_API.MenuItem | null => {
  if (menuItem.path) return menuItem

  if (!menuItem.children?.length) return null

  for (const child of menuItem.children) {
    if (child.path) return child

    if (child.children?.length) {
      const navigableChild = findFirstNavigableMenuItem(child)
      if (navigableChild) return navigableChild
    }
  }

  return null
}

// 
const handleMenuClick = (menuItem: Common_API.MenuItem) => {
  if (menuItem.path) {
    // 
    tabsStore.addTab({
      ...menuItem,
      path: menuItem.path,
      title: menuItem.title,
      name: menuItem.id,
    })
    // 
    router.push(menuItem.path)
    // 
    updateActiveTopMenu(menuItem.path)
  }
}

// 
const handleTabClose = (path: string) => {
  tabsStore.removeTab(path)
  // 
  if (path === route.path && tabsStore.tabs.length > 0) {
    const lastTab = tabsStore.tabs[tabsStore.tabs.length - 1]
    router.push(lastTab.path)
  }

  // 
  if (path.includes(ROUTER_PATH.patientDetail)) {
    patientInfoStore.closeAllDetailTabs(path.split('/')[3])
  }
}

interface TabItem {
  paneName: string
}

// 
const handleTabClick = (tab: TabItem) => {
  router.push(tab.paneName)
}

// // 
// const refreshCurrentPage = () => {
//   tabsStore.refreshTab(route.path)
// }

// 
const handleUserMenu = (e: string) => {
  if (e === 'logout') {
    userStore.logout()
    router.push('/login')
  }
  // else if (e === 'personalCenter') {
  //   ElMessage.warning('')
  // }
}

// 
const updateActiveTopMenu = (path: string) => {
  // 1.  activeMenu
  const activeMenuPath = route.meta?.activeMenu as string

  // 2.  activeMenu
  if (activeMenuPath) {
    const menuItem = menuItems.value.find((item) => item.path === activeMenuPath)
    if (menuItem) {
      activeTopMenu.value = menuItem.id
      return
    }
  }

  // 3.  activeMenu 
  const menuItem = menuItems.value.find((item) => item.path === path)
  if (menuItem) {
    activeTopMenu.value = menuItem.id
  }
}

// 
watch(
  () => route.fullPath, //  route 
  () => {
    if (
      route.meta?.title && // 
      route.path !== '/' && // 
      !route.path.includes('redirect') // 
    ) {
      const title =
        typeof route.meta.title === 'function'
          ? route.meta.title(route)
          : (route.meta.title as string)

      // 
      tabsStore.addTab({
        path: route.path,
        title,
        name: route.name as string,
        sticky: route.meta?.sticky,
      })
    }

    // 
    updateActiveTopMenu(route.path)
  },
  { immediate: true },
)

// 
router.beforeEach((to, from) => {
  console.log('Navigation guard:', {
    to: {
      path: to.path,
      fullPath: to.fullPath,
      name: to.name,
      params: to.params,
    },
    from: {
      path: from.path,
      fullPath: from.fullPath,
      name: from.name,
      params: from.params,
    },
  })
  return true
})
</script>

<template>
  <div class="basic-layout">
    <el-container class="layout-container">
      <!--  -->
      <el-header class="layout-header">
        <div class="header-left">
          <div class="flex align-center width280 mr4">
            <img :src="logo" alt="logo" class="logo" height="30" />
            <h1 class="system-title">{{ SYSTEM_CONFIG.TITLE }}{{ SYSTEM_CONFIG.VERSION }}</h1>
          </div>
          <!--  -->
          <el-menu
            mode="horizontal"
            :default-active="activeTopMenu"
            class="top-menu"
            @select="handleTopMenuSelect"
          >
            <el-menu-item v-for="item in menuItems" :key="item.id" :index="item.id">
              <el-icon>
                <component :is="menuIconIdMap[item.id]" />
              </el-icon>
              <span>{{ item.title }}</span>
            </el-menu-item>
          </el-menu>
        </div>
        <div class="header-right">
          <el-dropdown @command="handleUserMenu">
            <div class="user-info">
              <el-avatar :size="32" :src="avatarImg" />
              <span class="user-name">{{ userName }}</span>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <!-- <el-dropdown-item command="personalCenter"></el-dropdown-item> -->
                <el-dropdown-item command="logout"></el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>

          <!--  -->
          <el-space class="pl10 fs12" alignment="start" direction="vertical" :size="0">
            <span>
              {{
                SURGERY_ROOMS.find((item) => item.value === userStore.operatingRoom)?.label
              }}
            </span>
            <el-tooltip content="" placement="bottom">
              <span>{{ userInfo?.loginTime }} </span>
            </el-tooltip>
          </el-space>
          <!--  -->
          <el-tooltip
            effect="dark"
            :content="isFullscreen ? '' : ''"
            placement="bottom"
          >
            <el-icon class="pl20 cursor-pointer fs48" @click="toggleFullScreen">
              <FullScreen v-if="!isFullscreen" />
              <Aim v-else />
            </el-icon>
          </el-tooltip>
        </div>
      </el-header>

      <el-container class="main-container">
        <!--  -->
        <el-main class="layout-main">
          <!--  -->
          <div class="tabs-container" v-if="tabsStore.tabs.length > 0">
            <el-tabs
              v-model="tabsStore.activeTab"
              type="card"
              @tab-click="(pane) => handleTabClick(pane as any)"
              @tab-remove="(name) => handleTabClose(name as string)"
              class="page-tabs"
            >
              <el-tab-pane
                v-for="tab in tabsStore.tabs"
                :key="tab.path"
                :label="tab.title"
                :name="tab.path"
                :closable="!tab.sticky"
              />
            </el-tabs>
            <!-- <div class="tabs-actions">
              <el-button
                :icon="Refresh"
                circle
                size="small"
                @click="refreshCurrentPage"
                title=""
              />
            </div> -->
          </div>

          <!--  -->
          <div class="page-content">
            <router-view v-slot="{ Component, route }">
              <transition name="fade-transform" mode="out-in">
                <keep-alive :include="tabsStore.cachedViews">
                  <component
                    :is="Component"
                    :key="route.meta?.key ? route.meta.key(route) : route.path"
                  />
                </keep-alive>
              </transition>
            </router-view>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

